.Globe {
    position: fixed;
    height: 100%;
    width: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(67, 124, 199)), to(#111)) repeat scroll 0 0 #222;
    background: -webkit-linear-gradient(rgb(67, 124, 199), #111) repeat scroll 0 0 #222;
    background: -o-linear-gradient(rgb(67, 124, 199), #111) repeat scroll 0 0 #222;
    background: linear-gradient(rgb(67, 124, 199), #111) repeat scroll 0 0 #222;
}



